import React, { useState, useEffect } from 'react';
import { withRouter } from 'react-router';
import { useDispatch, useSelector } from 'react-redux';

import * as Yup from 'yup';
import { Formik } from 'formik';
import { axios } from '../../../http';
import SetTokenHeader from '../../../hoc/SetTokenHeader/SetTokenHeader';
import CardWithBack from '../../../components/CardWithBack/CardWithBack';
import AlertMessageModal from '../../../components/UI/Model/AlertMessageModal';

import './vehicle.scss';

import {
  addOwnerVehicleSaga,
  getAllServiceSaga,
  getAllVehiclesSaga,
} from '../../../store/actions';

const AddVehicle = props => {
  const [vehiclePhotosUrl, setVehiclePhotosUrl] = useState([]);
  const [insuranceImageUrl, setInsuranceImageUrl] = useState([]);
  const [pucImageUrl, setPucImageUrl] = useState([]);
  const dispatch = useDispatch();
  const { open } = useSelector(state => state.modal);
  const { firstName, userId } = useSelector(state => state.auth);
  const {
    vehicles: { list: vehicleOptions },
  } = useSelector(state => state.vehicleTypes);
  const {
    services: { list: serviceOptions },
  } = useSelector(state => state.service);

  const tableFilter = {
    columnName: 'name',
    orderBy: 'asc',
    search: '',
    skip: 0,
    limit: 10,
  };

  const validationSchema = Yup.object({
    // name: Yup.string().trim().required('Vehicle Name is Required'),
    // vehicleTypeId: Yup.string().required('Please Select Vehicle Type'),
    // serviceId: Yup.string().required('Please Select Service Type'),
    // vehicleNumber: Yup.string().trim().required('Please Enter Vehicle Number'),
  });

  const submitButtonHandler = values => {
    values.ownerId = userId;
    console.log(values);
    dispatch(
      addOwnerVehicleSaga({
        formValues: values,
        tableFilter,
      }),
    );
  };
  const getData = (columnName, orderBy, search, skip, limit) => {
    dispatch(getAllVehiclesSaga({ columnName, orderBy, search, skip, limit }));
    dispatch(getAllServiceSaga({ columnName, orderBy, search, skip, limit }));
  };

  useEffect(() => {
    getData('name', 'asc', '', 0, 5000);
  }, []);

  return (
    <>
      <CardWithBack
        pageTitle={
          props.history.location.pathname.includes('add')
            ? 'Add Vehicle'
            : 'Edit Vehicle'
        }
        dataLoadeed={typeof 1 === typeof 2}
        errorMsgAddPromoCode=""
      >
        <Formik
          enableReinitialize
          initialValues={{
            ownerName: firstName,
            name: '',
            modelName: '',
            mfgYear: '',
            colour: '',
            vehicleManufacturer: '',
            fuelType: '',
            vehicleNumber: '',
            insuranceNumber: '',
            insuranceStartDate: '',
            insuranceEndDate: '',
            insuranceImages: '',
            vehicleImages: '',
            pucImages: '',
            vehicleTypeId: '',
            serviceId: '',
          }}
          validationSchema={validationSchema}
          onSubmit={(values, { setSubmitting }) => {
            console.log(values);
            submitButtonHandler(values);
            setSubmitting(false);
          }}
        >
          {({
            values,
            errors,
            touched,
            handleChange,
            handleBlur,
            handleSubmit,
            setFieldValue,
            setFieldTouched,
            // isSubmitting,
          }) => (
            <form onSubmit={handleSubmit}>
              {/* <div className="row"> */}
              <div className="row">
                <div className="col-xs-12 col-lg-4 col-md-6 col-sm-6">
                  <div className="from-group text-left">
                    <h4 className="required-field">Vehicle Name:</h4>
                    <input
                      type="text"
                      name="name"
                      placeholder="Enter Vehicle Name"
                      className="form-control"
                      value={values.name}
                      // onChange={e => setFirstName(e.target.value.trim())}
                      onChange={handleChange}
                      onBlur={handleBlur}
                    />
                    <div className="error-message">
                      {errors.name && touched.name && errors.name}
                    </div>
                  </div>
                </div>
                <div className="col-xs-12 col-lg-4 col-md-6 col-sm-6">
                  <div className="from-group text-left">
                    <h4 className="required-field">Vehicle Model:</h4>
                    <input
                      type="text"
                      name="modelName"
                      placeholder="Enter Vehicle Model"
                      className="form-control"
                      value={values.modelName}
                      // onChange={e => setFirstName(e.target.value.trim())}
                      onChange={handleChange}
                      onBlur={handleBlur}
                    />
                    <div className="error-message">
                      {errors.modelName &&
                        touched.modelName &&
                        errors.modelName}
                    </div>
                  </div>
                </div>
                <div className="col-xs-12 col-lg-4 col-md-6 col-sm-6">
                  <div className="from-group text-left">
                    <h4 className="required-field">Vehicle Owner Name:</h4>
                    <input
                      type="text"
                      name="ownerName"
                      placeholder="Vehicle Owner Name"
                      className="form-control"
                      disabled
                      value={values.ownerName}
                      // onChange={e => setFirstName(e.target.value.trim())}
                      onChange={handleChange}
                      onBlur={handleBlur}
                    />
                    <div className="error-message">
                      {errors.ownerName &&
                        touched.ownerName &&
                        errors.ownerName}
                    </div>
                  </div>
                </div>
              </div>
              <div className="row">
                <div className="col-xs-12 col-lg-3 col-md-6 col-sm-6">
                  <div className="from-group text-left">
                    <h4 className="required-field">Year of Manufacture:</h4>
                    <input
                      type="text"
                      name="mfgYear"
                      placeholder="Enter Year of Manufacture"
                      className="form-control"
                      value={values.mfgYear}
                      // onChange={e => setFirstName(e.target.value.trim())}
                      onChange={handleChange}
                      onBlur={handleBlur}
                    />
                    <div className="error-message">
                      {errors.mfgYear && touched.mfgYear && errors.mfgYear}
                    </div>
                  </div>
                </div>
                <div className="col-xs-12 col-lg-3 col-md-6 col-sm-6">
                  <div className="from-group text-left">
                    <h4 className="required-field">Vehicle Colour:</h4>
                    <input
                      type="text"
                      name="colour"
                      placeholder="Vehicle Colour"
                      className="form-control"
                      value={values.colour}
                      // onChange={e => setFirstName(e.target.value.trim())}
                      onChange={handleChange}
                      onBlur={handleBlur}
                    />
                    <div className="error-message">
                      {errors.colour && touched.colour && errors.colour}
                    </div>
                  </div>
                </div>
                <div className="col-xs-12 col-lg-3 col-md-6 col-sm-6">
                  <div className="from-group text-left">
                    <h4 className="required-field">Vehicle Manufacturer:</h4>
                    <input
                      type="text"
                      name="vehicleManufacturer"
                      placeholder="Enter Vehicle Manufacturer"
                      className="form-control"
                      value={values.vehicleManufacturer}
                      // onChange={e => setFirstName(e.target.value.trim())}
                      onChange={handleChange}
                      onBlur={handleBlur}
                    />
                    <div className="error-message">
                      {errors.vehicleManufacturer &&
                        touched.vehicleManufacturer &&
                        errors.vehicleManufacturer}
                    </div>
                  </div>
                </div>
                <div className="col-xs-12 col-lg-3 col-md-6 col-sm-6">
                  <div className="from-group text-left">
                    <h4 className="required-field">Vehicle Fuel Type:</h4>
                    <select
                      name="fuelType"
                      id=""
                      className="form-control"
                      value={values.fuelType}
                      onChange={event => {
                        setFieldValue('fuelType', event.target.value);
                      }}
                      onBlur={() => setFieldTouched('fuelType', true)}
                    >
                      <option value="" disabled>
                        Select Fuel Type
                      </option>
                      <option value="petrol">Petrol</option>
                      <option value="diesel">Diesel</option>
                    </select>
                    <div className="error-message">
                      {errors.fuelType && touched.fuelType && errors.fuelType}
                    </div>
                  </div>
                </div>
              </div>
              <div className="row">
                <div className="col-xs-12 col-lg-3 col-md-6 col-sm-6">
                  <div className="from-group text-left">
                    <h4 className="required-field">Vehicle Type:</h4>
                    <select
                      className="form-control"
                      name="vehicleTypeId"
                      placeholder="Select category"
                      value={values.vehicleTypeId}
                      onChange={event => {
                        setFieldValue('vehicleTypeId', event.target.value);
                      }}
                      onBlur={() => setFieldTouched('vehicleTypeId', true)}
                    >
                      <option value="" disabled>
                        Select Category
                      </option>
                      {vehicleOptions.map(item => {
                        if (item.isBlock === false) {
                          return (
                            <option key={item._id} value={item._id}>
                              {item.type}
                            </option>
                          );
                        }
                        return null;
                      })}
                    </select>
                    <div className="error-message">
                      {errors.vehicleTypeId &&
                        touched.vehicleTypeId &&
                        errors.vehicleTypeId}
                    </div>
                  </div>
                </div>
                <div className="col-xs-12 col-lg-3 col-md-6 col-sm-6">
                  <div className="from-group text-left">
                    <h4 className="required-field">Select Service Type:</h4>
                    <select
                      className="form-control"
                      name="serviceId"
                      placeholder="Select category"
                      value={values.serviceId}
                      onChange={event => {
                        setFieldValue('serviceId', event.target.value);
                      }}
                      onBlur={() => setFieldTouched('serviceId', true)}
                    >
                      <option value="" disabled>
                        Select Service
                      </option>
                      {serviceOptions.map(item => {
                        if (item.status === true) {
                          return (
                            <option key={item._id} value={item._id}>
                              {item.name}
                            </option>
                          );
                        }
                        return null;
                      })}
                    </select>
                    <div className="error-message">
                      {errors.serviceId &&
                        touched.serviceId &&
                        errors.serviceId}
                    </div>
                  </div>
                </div>
                <div className="col-xs-12 col-lg-6 col-md-6 col-sm-6">
                  <div className="from-group text-left">
                    <h4 className="required-field">Vehicle Number:</h4>
                    <input
                      type="text"
                      name="vehicleNumber"
                      placeholder="Enter Vehicle Number"
                      className="form-control"
                      value={values.vehicleNumber}
                      // onChange={e => setFirstvehicleNumber(e.target.value.trim())}
                      onChange={handleChange}
                      onBlur={handleBlur}
                    />
                    <div className="error-message">
                      {errors.vehicleNumber &&
                        touched.vehicleNumber &&
                        errors.vehicleNumber}
                    </div>
                  </div>
                </div>
              </div>
              {/* Insurance */}
              <div className="row">
                <div className="col-xs-12 col-lg-6 col-md-6 col-sm-6">
                  <div className="from-group text-left">
                    <h4 className="required-field">Insurance Policy Number:</h4>
                    <input
                      type="text"
                      name="insuranceNumber"
                      placeholder="Enter Insurance Policy Number"
                      className="form-control"
                      value={values.insuranceNumber}
                      // onChange={e => setFirstinsuranceNumber(e.target.value.trim())}
                      onChange={handleChange}
                      onBlur={handleBlur}
                    />
                    <div className="error-message">
                      {errors.insuranceNumber &&
                        touched.insuranceNumber &&
                        errors.insuranceNumber}
                    </div>
                  </div>
                </div>
                <div className="col-xs-12 col-lg-3 col-md-6 col-sm-6">
                  <div className="from-group text-left">
                    <h4 className="required-field">
                      Insurance Policy Start Date:
                    </h4>
                    <input
                      type="date"
                      name="insuranceStartDate"
                      placeholder="Vehicle Owner Name"
                      className="form-control"
                      value={values.insuranceStartDate}
                      // onChange={e => setFirstName(e.target.value.trim())}
                      onChange={handleChange}
                      onBlur={handleBlur}
                    />
                    <div className="error-message">
                      {errors.insuranceStartDate &&
                        touched.insuranceStartDate &&
                        errors.insuranceStartDate}
                    </div>
                  </div>
                </div>
                <div className="col-xs-12 col-lg-3 col-md-6 col-sm-6">
                  <div className="from-group text-left">
                    <h4 className="required-field">
                      Insurance Policy End Date:
                    </h4>
                    <input
                      type="date"
                      name="insuranceEndDate"
                      placeholder="Vehicle Owner Name"
                      className="form-control"
                      value={values.insuranceEndDate}
                      // onChange={e => setFirstName(e.target.value.trim())}
                      onChange={handleChange}
                      onBlur={handleBlur}
                    />
                    <div className="error-message">
                      {errors.insuranceEndDate &&
                        touched.insuranceEndDate &&
                        errors.insuranceEndDate}
                    </div>
                  </div>
                </div>
              </div>
              <div className="row">
                <div className="col-xs-12 col-lg-3 col-md-6 col-sm-3">
                  <div className="from-group text-left">
                    <h4 className="required-field">Upload Vehicle Image: </h4>
                    <input
                      type="file"
                      name="vehicleImages"
                      id="photo1"
                      multiple
                      accept="image/x-png ,image/png, image/jpeg, images/gif"
                      onChange={e => {
                        const { files } = e.target;
                        if (files) {
                          const fileArray = Array.from(files).filter(file =>
                            file.type.includes('image'),
                          );
                          const fileArrayBlob = fileArray.map(file =>
                            URL.createObjectURL(file),
                          );
                          setFieldValue('vehicleImages', fileArray);
                          setVehiclePhotosUrl([...fileArrayBlob]);
                        }
                      }}
                      // onChange={handleChange}
                    />

                    <div className="error-message">
                      {errors.vehicleImages &&
                        touched.vehicleImages &&
                        errors.vehicleImages}
                    </div>
                  </div>
                </div>
                <div className="col-xs-12 col-lg-3 col-md-6 col-sm-3">
                  <div className="from-group text-left">
                    <h4 className="required-field">Upload Insurance Image: </h4>
                    <input
                      type="file"
                      name="insuranceImages"
                      id="photo1"
                      multiple
                      accept="image/x-png ,image/png, image/jpeg, images/gif"
                      onChange={e => {
                        const { files } = e.target;
                        if (files) {
                          const fileArray = Array.from(files).filter(file =>
                            file.type.includes('image'),
                          );
                          const fileArrayBlob = fileArray.map(file =>
                            URL.createObjectURL(file),
                          );
                          setFieldValue('insuranceImages', fileArray);
                          setInsuranceImageUrl([...fileArrayBlob]);
                        }
                      }}
                      // onChange={handleChange}
                    />

                    <div className="error-message">
                      {errors.insuranceImages &&
                        touched.insuranceImages &&
                        errors.insuranceImages}
                    </div>
                  </div>
                </div>
                <div className="col-xs-12 col-lg-3 col-md-6 col-sm-3">
                  <div className="from-group text-left">
                    <h4 className="required-field">Upload PUC Image: </h4>
                    <input
                      type="file"
                      name="pucImages"
                      id="photo1"
                      multiple
                      accept="image/x-png ,image/png, image/jpeg, images/gif"
                      onChange={e => {
                        const { files } = e.target;
                        if (files) {
                          const fileArray = Array.from(files).filter(file =>
                            file.type.includes('image'),
                          );
                          const fileArrayBlob = fileArray.map(file =>
                            URL.createObjectURL(file),
                          );
                          setFieldValue('pucImages', fileArray);
                          setPucImageUrl([...fileArrayBlob]);
                        }
                      }}
                      // onChange={handleChange}
                    />

                    <div className="error-message">
                      {errors.pucImages &&
                        touched.pucImages &&
                        errors.pucImages}
                    </div>
                  </div>
                </div>
              </div>
              {vehiclePhotosUrl.length > 0 && (
                <div className="row">
                  <div className="col-xs-12 col-lg-12 col-md-6 col-sm-3">
                    <h4>preview Vehicle Images</h4>
                    <div className="image-container">
                      {vehiclePhotosUrl.map(img => (
                        <img
                          className="vehicle-photo-preview"
                          key={img}
                          alt="dishImage"
                          src={img}
                        />
                      ))}
                    </div>
                  </div>
                </div>
              )}
              {insuranceImageUrl.length > 0 && (
                <div className="row">
                  <div className="col-xs-12 col-lg-12 col-md-6 col-sm-3">
                    <div className="from-group text-left">
                      <h4>Preview Insurance Images</h4>
                      {insuranceImageUrl.map(img => (
                        <img
                          className="vehicle-photo-preview"
                          key={img}
                          alt="dishImage"
                          src={img}
                        />
                      ))}
                    </div>
                  </div>
                </div>
              )}
              {pucImageUrl.length > 0 && (
                <div className="row">
                  <div className="col-xs-12 col-lg-12 col-md-6 col-sm-3">
                    <h4>Preview PUC Images</h4>
                    {pucImageUrl.map(img => (
                      <img
                        className="vehicle-photo-preview"
                        key={img}
                        alt="dishImage"
                        src={img}
                      />
                    ))}
                  </div>
                </div>
              )}
              {/* </div> */}
              <div className="row">
                <div className="form-group">
                  <button
                    type="submit"
                    className="btn btn-primary m-l-sm m-b-md"
                    style={{ marginTop: '20px' }}
                  >
                    Add Vehicle
                  </button>
                </div>
              </div>
            </form>
          )}
        </Formik>
      </CardWithBack>
      {open && <AlertMessageModal />}
    </>
  );
};

export default SetTokenHeader(withRouter(AddVehicle), axios);
